<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>入住商家账户资产管理</li>
      <li>支持保证金额调整，查阅账户明细信息</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>商家账号</label>
        <el-input v-model="queryParams.storeUsername" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      ref="dataTable"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="store.storeName" label="店铺名称" min-width="180"></el-table-column>
      <el-table-column prop="store.storeUsername" label="商家账号" min-width="160"></el-table-column>
      <el-table-column prop="balance" label="账户金额" min-width="160">
        <template slot-scope="scope">{{$cms.float(scope.row.balance)}}</template>
      </el-table-column>
      <el-table-column prop="frozenMoney" label="冻结金额" min-width="110">
        <template slot-scope="scope">{{$cms.float(scope.row.frozenMoney)}}</template>
      </el-table-column>
      <el-table-column prop="bondMoney" label="保证金" min-width="110">
        <template slot-scope="scope">{{$cms.float(scope.row.bondMoney)}}</template>
      </el-table-column>
      <el-table-column label="操作" width="340" fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon="edit" size="small" type="info" @click="adjust(scope.row.id)">资金调整</cms-button>
          <cms-button
            icon="edit"
            size="small"
            type="info"
            @click.stop="$routerLink('/shopAccount/accountList/detali','list',{ id:scope.row.store.id })"
          >账户明细</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>

    <el-dialog title="资金调整" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="formData" :rules="saveRules">
        <el-form-item label="店铺名称" prop="storeName" label-width="120px">
          <template>
            <span v-if="formData.storeName">{{formData.storeName}}</span>
          </template>
        </el-form-item>
        <el-form-item label="资金类型" prop="capitalChangeType" label-width="120px">
          <el-radio-group v-model="formData.capitalChangeType">
            <el-radio :label="2">保证金</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="调整类型" prop="calculatorType" label-width="120px">
          <el-radio-group v-model="formData.calculatorType">
            <el-radio :label="1">增加</el-radio>
            <el-radio :label="2">减少</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="调整金额" prop="money" label-width="120px">
          <el-input maxlength="6" v-model="formData.money"></el-input>
        </el-form-item>
        <el-form-item label="原由" prop="remark" label-width="120px">
          <el-input maxlength="255" v-model="formData.remark" type="textarea" :rows="6"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="noPass">提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import rules from '@/rules'
import list from '@/mixins/list'
import form from '@/mixins/form.js'
import {
  accountList,
  accountUpdate,
  accountSelectGet
} from '@/api/shop/shopAccount.js'
var defaultData = {
  storeUsername: '',
  size: 10,
  page: 1
}
var fundData = {
  id: '',
  capitalChangeType: 2,
  calculatorType: 1,
  storeName: '',
  money: '',
  remark: ''
}
export default {
  mixins: [list, form],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      formData: Object.assign({}, fundData),
      dialogFormVisible: false,
      saveRules: {
        // 新增规则
        capitalChangeType: [rules.required('此项必填', 'change')],
        calculatorType: [rules.required('此项必填')],
        money: [rules.required('此项必填'), rules.float()],
        remark: [rules.required('此项必填')]
      }
    }
  },
  methods: {
    // 点击资金调整按钮方法
    adjust(id) {
      this.dialogFormVisible = true
      accountSelectGet({ id: id }).then(res => {
        // 通过id获取信息
        this.formData = Object.assign(fundData, res.data)
      })
    },
    // 资金调整提交
    noPass() {
      this.saveForm(accountUpdate, this.formData)
        .then(res => {
          if (res.code === 200) {
            this.$successMessage('提交成功')
            this.getTableData(accountList, this.queryParams)
            this.dialogFormVisible = false
          } else {
            this.$errorMessage('提交失败,' + res.message)
          }
        })
        .catch(() => {})
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(accountList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(accountList, this.queryParams)
  }
}
</script>

<style>
</style>



